@media (min-width: 1200px) and (max-width: 1900px) {
	.header{
		&__items {
			fieldset {
				padding: 15px 0 3px 0;
				font-size: 2em;
			}
		}
		&__apruved {
			width: 300px;
			height: 300px;
		}
	}
	.skills {
		&__title {
			p {
				font-size: 2em;
			}	
		}
		&__item {
			display: block;
			width: 15%;
			margin: 0 auto;
			padding-left: 35px;
			font-size: 1.3em;
		}
		&__checkbox {
			width: 30px;
			height: 30px;
			&:after {
				left: 12px;
				top: 3px;
				height: 20px;
			}
		}
	}
	.lvl {
		&__wrapper {
			height: 30vh;
			justify-content: space-around;
		}
		&__items {
			width: 400px;
			height: 230px;
			&-scale {
				width: 100%;
				height: 100%;
			}
			&-pointer {
				width: 80px;
				height: 70px;
				left: 120px;
				bottom: -10px;
			}
		}
		&__index {
			&-name {
				font-size: 2em;
			}
			&-score {
				font-size: 5em;
			}
		}
	}
}