.lvl {
	&__wrapper {
		height: 22vh;
		display: flex;
		justify-content: space-around;
		align-items: flex-start;
	}
	&__items {
		position: relative;
		&-scale {
			width: 250px;
			height: 130px;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&-pointer{
			position: absolute;
			bottom: 0;
			left: 80px;
			width: 60px;
			height: 50px;
			transform: rotate(-60deg);
			img {
				width: 100%;
				height: 100%;
			}
		}
	}
	&__index {
		display: flex;
		flex-direction: column;
		width: 30%;
		margin-left: 7vw;
		&-name {
			height: 50px;
			font-size: 1.4em;
			margin-bottom: 10px;
			transition: all 0.4s;
		}
		&-score {
			font-size: 3.5em;
			font-weight: bold;
			transition: all 1s;
			color: #0093d7;
		}
	}

}