@media (min-width: 300px) and (max-width: 649px) {
	body {
		padding: 6vw ;
	}
	.header {
		&__wrapper {
			flex-direction: column;
		}
		&__logo {
			width: 50%;
			height: 50%;
			margin: 0 auto;
		}
		&__apruved {
			width: 35vw;
			height: 60vh;
			opacity: .5;
			top: 180px;
		}
		&__items {
			margin: 10px 0 0 0;
			font-size: 4vw;
		}
	}
	.skills {
		&__item {
			width: 50%;
			font-size: 0.8em;
		}
	}
	.lvl {
		&__wrapper {
			height: 100%;
			flex-direction: column;
		}
		&__items {
			text-align: center;
			margin: 0 auto;
		}
		&__index {
			width: 100%;
			text-align: center;
			margin: 0 auto;
			margin-top: 50px;
			&-name {
				font-size: 4vw;
				margin-bottom: 10px;
			}
		}
	}
}