@media (min-width: 650px) and (max-width: 830px) {
	.header {
		&__wrapper {
			flex-direction: column;
		}
		&__logo {
			width: 40%;
			height: 40%;
			margin: 0 auto;
		}
		&__apruved {
			width: 25vw;
			height: 40vh;
			opacity: .5;
			top: 180px;
		}
		&__items {
			margin: 10px 0 0 0;
			font-size: 2vw;
		}
	}
	.skills {
		&__item {
			width: 50%;
			font-size: 0.8em;
		}
	}
	.lvl {
		&__wrapper {
			height: 100%;
			flex-direction: column;
		}
		&__items {
			text-align: center;
			margin: 0 auto;
		}
		&__index {
			width: 100%;
			text-align: center;
			margin: 0 auto;
			margin-top: 50px;
			&-name {
				font-size: 2.5vw;
				margin-bottom: 10px;
			}
		}
	}
}