.header {
	&__wrapper {
		width: 100%;
		height: 25%;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: flex-start;
	}
	&__logo {
		width: 30%;
		height: 30%;
		&-link {
			width: 100%;
			height: 100%;
		}
		&-img {
			display: block;
			margin: 0 auto;
			width: 100%;
			height: 100%;
			border-radius: 50%;
		}
	}
	&__items {
		width: 100%;
		margin-left: 15px;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		fieldset {
			display: block;
			width: 100%;
			border: none;
			border-bottom: 2px solid;
			padding: 0 0 3px 0;
			margin-bottom: 25px;
			font-size: 1.2em;
			&:last-of-type {
				margin-bottom: 0px;
			}
			input {
				width: 100%;
				outline: none;
				border: none;
				background: none;
				&::placeholder {
					color: #000000;
				}
			}
		}
	}
	&__apruved {
		width: 180px;
		height: 180px;
		position: absolute;
		right: 2%;
		top: 65px;
		z-index: 100;
		background: url(../images/sprites/png/header.png) center no-repeat;
		background-size: 100%;
	}
}