.skills {
	&__title {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		p {
			font-size: 1.2em;
			font-weight: bold;
		}
	}
	&__items {
		height: 26vh;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
		align-content: stretch;
	}
	&__item {
		display: flex;
		position: relative;
		width: 140px;
		font-size: 0.9em;
		padding-left: 25px;
		padding-top: 3px;
		transition: all 1s;
		z-index: 10;
		&:hover {
			color: #0093d7;
			text-shadow: 5px 0 10px #000000;
			cursor: pointer;
		}
	}
	&__disabled {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
		&:checked ~ .skills__checkbox {
			background-color: #0093d7;
		}
		&:checked ~ .skills__checkbox:after {
			display: block;
		}
	}
	&__checkbox {
		position: absolute;
		top: 0;
		left: 0;
		height: 20px;
		width: 20px;
		border-radius: 5px;
		background-color: #eee;
		cursor:default;
		pointer-events: none;
		z-index: 5;
		&::after {
			content: "";
			position: absolute;
			display: none;
			left: 7px;
			top: 2px;
			width: 7px;
			height: 15px;
			border: solid white;
			border-width: 0 3px 3px 0;
			-webkit-transform: rotate(45deg);
			-ms-transform: rotate(45deg);
			transform: rotate(45deg);
			cursor:default;
			pointer-events: none;
		}
	}
}